import React from 'react';
import { useEffect } from 'react';
import './index.css';
import 'jquery-ui-dist/jquery-ui';
import $ from 'jquery';


function Sidebar() {

    useEffect(() => {
        $(document).ready(function () {
            $('.sub-btn').click(function () {
                $(this).next('.sub-menu').slideToggle()
            })
        })
    }, [])

  



    return (
        <>
            <div className='sidebar'>
                <div className="menu">
                    <div className="item"> <a className='sub-btn' href="#" >  Types   </a>
                        <div className="sub-menu">
                            <a className='sub-item' href="/"> LongTerm </a>
                            <a className='sub-item' href="/ShortTerm"> ShortTerm </a>
                        </div>
                    </div>
                    <div className="item"> <a className='sub-btn' href="#" >  Variables </a>
                        <div className="sub-menu">
                            <a className='sub-item' href="/" > High </a>
                            <a className='sub-item' href="/LOW"> Low </a>
                            <a className='sub-item' href="/OPEN"> Open </a>
                            <a className='sub-item' href="/CLOSE"> Close </a>
                        </div>
                    </div>
                    <div className="item"> <a href="#" >  Profile </a> </div>
                    <div className="item"> <a href="#" >  About </a> </div>
                </div>
            </div>

            <script>
                {/* $(document).ready(function(){
                    $('.sub-btn').click(function(){
                        $(this).next('.sub-menu').slideToggle()
                    })   
                }); */}
            </script>
        </>
    )
}

export default Sidebar;










