import React from 'react'
import Header from '../../Header'
import Sidebar from '../../Sidebar'
import Home from '../../Home'

const OPEN = () => (
    
    <>
     <Header/>
     <div className='Container'>
       <Sidebar/>
       <Home 
       variable={'OPEN'}
       />
     </div>
   </>
    
  )


export default OPEN